<template>
	<div class="large-deposit-buy-detail">
		<mt-cell title="" :value="detail.number"></mt-cell>
		<mt-cell title="" :value="detail.company"></mt-cell>
		<mt-cell title="" :value="detail.name"></mt-cell>
		<mt-cell title="" :value="detail.money1"></mt-cell>
		<mt-cell title="" :value="detail.money2"></mt-cell>
		<mt-cell title="" :value="detail.rate1"></mt-cell>
		<mt-cell title="" :value="detail.date"></mt-cell>
		<mt-cell title="" :value="detail.date1"></mt-cell>
		<mt-cell title="" :value="detail.date2"></mt-cell>
		<mt-cell title="" :value="detail.type"></mt-cell>
		<mt-cell title="" :value="detail.transfer"></mt-cell>
		<mt-cell title="" :value="detail.rate2"></mt-cell>
		<mt-cell title="" :value="detail.account"></mt-cell>
        <mt-field v-model="buymoney" label="" type="number"></mt-field>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<mt-button @click.native="submit" type="primary"></mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default {
	data() {
		return {
			detail: {
				number: '',
				company: '',
				name: '',
				money1: '',
				money2: '',
				rate1: '',
				date: '',
				date1: '',
				date2: '',
				type: '',
				transfer: '',
				rate2: '',
				account: ''
			},
			buymoney: ''
		};
	},
	methods: {
		submit() {
			this.$router.push(`/large-deposit/buy/buy/${this.account}`);
		},
		back() {
			history.back();
		},
		getBuyProductDetail(account) {
			service.getBuyProductDetail({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getBuyProductDetail(this.$route.params.account);
	}
};

</script>


